import { QuestComponent } from '../../components';
import { useNavigate, useParams } from 'react-router-dom';
import { useAppLocations } from '../../hooks';
import { FC, useEffect, useState } from 'react';
import { IQuest } from '../../types/quest.interface';
import { questService } from '../../services';
import css from './SQP.module.css';

const SingleQuestPage: FC = () => {
    const {id} = useParams<{ id: string }>();
    const {state} = useAppLocations<IQuest>();
    const [quest, setQuest] = useState<IQuest | null>(null);
    const navigate = useNavigate();

    useEffect(() => {
        if (state){
            setQuest(state)
        }else {
            questService.getById(id!).then(({data})=>setQuest(data))
        }
    }, [id, state])

    return (
        <div className={css.container}>
                    <button onClick={()=> navigate(-1)}>BACK</button>
            {quest&& (
                <div className={css.child}>
                    <div> : {quest.firstName}</div>
                    <div> : {quest.phoneNumber}</div>
                    <div>email: {quest.email}</div>
                    <div> ': {quest.chanelSpeak}</div>
                    <div> : {quest.visitSpeak}</div>
                    <div> : {quest.companyName}</div>
                    <div>  : {quest.serviceArea}</div>
                    <div> : {quest.uniqueProduct}</div>
                    <div> : {quest.currentWeb}</div>
                    <div> : {quest.needToDo}</div>
                    <div> : {quest.term}</div>
                    <div>: {quest.budget}</div>
                    <div>: {quest.start}</div>
                    <div>: {quest.aboutClients}</div>
                    <div>: {quest.aboutCompetitors}</div>
                    <div>: {quest.designWeb}</div>
                    <div>: {quest.elementsWeb}</div>
                    <div>: {quest.pagesWeb}</div>
                    <div>: {quest.functionalWeb}</div>
                    <div>: {quest.contentWeb}</div>
                    <div>: {quest.forgot}</div>
                </div>
            )}
        </div>
    );
};

export { SingleQuestPage };